.header {
    background: -webkit-gradient(linear, left top, right top, from(rgb(189, 185, 185)), to(rgb(127, 177, 170)));
    background: -webkit-linear-gradient(left, rgb(189, 185, 185), rgb(127, 177, 170));
    background: -o-linear-gradient(left, rgb(189, 185, 185), rgb(127, 177, 170));
    background: linear-gradient(to right, rgb(189, 185, 185), rgb(127, 177, 170));
    height: 70px;
    text-align: center;
    padding-top: 23px;
    font-family: arial;
    font-weight: 400;
    color: white;
}

#home, #center-text {
    display: inline-block;
    font-family: arial;
    margin-right: 60px;
}

#home {
    color: rgb(255, 255, 255);
    font-weight: 1000;
    font-size: 20px
}

#score {
    display: inline-block;
    background-color: rgb(31, 128, 225);
    border-radius: 40px;
    padding: 0 15px;
    -webkit-box-shadow: 0px 0px 9px rgb(57, 98, 160);
            box-shadow: 0px 0px 9px rgb(57, 98, 160);
    
}

p {
    display: inline-block;
}

#hs {
    margin-left: 15px;
}


